*{
  box-sizing: border-box;
}
body {
  width: 100vw;
  height: 100vh;
  font-family: sans-serif;
  overflow: hidden; 
}
.line{
  width: 100vw;
}
.message{
  width: 80vw;
  margin: auto auto;
  padding:2%;
  text-align: center;
}
.addContent{
  background-color: #68C4CA;
  color: white;

}
.addUsers{
  background-color: #84CA9D;
  color: white;

}
.formations,.help{
  background-color: #E5E5E5;
  color: black;
}

.dashboard{
  display: inline-grid;
  margin-left: 15%;

.card {
  width: 70vw;
  height: 15vh;
  border-radius: 6px;
  margin-bottom: 10%;
  display: flex;
  align-items: baseline;
}
.card>*{
  margin: auto 0;
  margin-left:10%;
}
.icon{
  width: 10%;
}
.text{
  font-size: 16px;
  width: 60%;
  word-wrap: break-word;
  padding: 2%;
  text-align: center;
  margin: auto auto;
}
}

@media screen and (min-width:400px){
  .text{
    font-size: 17px!important;
  }
  .message{
    font-size: 14px;
  }
}

@media screen and (min-width:500px){
  .text{
    font-size: 18px!important;
  }
  .message{
    font-size: 16px;
  }
}

@media screen and (min-width:600px){
  .dashboard{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 20px;
    grid-column-gap: 5rem;
    margin: 15% 10% 0 10%;
}
.card{
  margin: 0;
  width: 90%!important;
}
.icon{
  width: 15%!important;
}
}

@media screen and (min-width:800px){
  .icon{
    width: 10% !important;
  }
}

@media screen and (min-width:600px){
  p{
    font-size: 18px;
  }
}